@import '../appBasicStyleSetting';

.app-addOnInfo {
  display: flex;
  align-items: baseline;
  flex-direction: column; }

.app-addOnInfo--title {
  flex: 2; }

.app-addOnInfo--unitQty {
  flex: 4;
  width: 100%;
  margin-right: 5px;
  display: flex;
  align-items: center; }

.app-addOnInfo--unitQty--title {
  color: $potmasterTitleColor;
  flex: 2;
  text-align: right; }

.app-addOnInfo--unitQty--input {
  flex: 3;

  input ,p {
    text-align: center; } }

.app-addOnInfo--note {
  flex: 5;
  width: 100%; }


.app-addOnOptionSetting {
  display: flex;
  flex-direction: column; }

.app-addOnOptionSetting--title {
  flex: 1; }

.app-addOnOptionSetting--value {
  flex: 3; }

.app-addOnOptionSetting--note {
  flex: 3; }


@media screen and (min-width: 768px) {
  .app-addOnInfo,.app-addOnOptionSetting {
    flex-direction: row;
    align-items: center; } }

